
.menucontainer{
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    /* justify-content: space-between; */
    align-items: center;
    justify-content: center;
    width:100%;
    height: 100%;
    /* gap:75px; */
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    overflow-x:hidden;
    background-color: rgba(39, 37, 37, 0.966);
    /* border:2px solid red; */
    /* margin-top: 20px; */
    /* padding-top: 20px; */
    /* color: white !important; */
    /* padding-bottom: 10px; */
    /* padding: 0; */
}

.slider{
    display: flex;
    overflow-x:hidden;
    scroll-snap-type: x mandatory;
    scroll-behavior: smooth;
    border-radius: 0.5rem;
    height: 240px;
    /* border:2px solid red; */
    /* margin-bottom: 100px; */

}

menu{
    width: 100%;
    /* border:2px solid red; */
    height: 50px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
}

menu button{
    /* border:2px solid red; */
    width: auto;
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    color: bisque;
    font-size: 18px;
    font-weight: 600;
    border-radius: 5px;
    margin-inline:3px ;
}
 
button.active{
    background-color: rgb(8, 7, 7);
}

menu button:hover{
    background-color: rgb(19, 18, 18);
    border-radius: 5px;
}



.slider-image{
    flex: 1 0 100%;
    scroll-snap-align:start;
    object-fit: cover;
    /* animation: scroll 80s linear infinite ; */
    
}
.card-carousel img{
    width: 100%;
    object-fit: cover;
}
.container{
    height: 100vh;
    width: 100%;
 
}



.category{
    width: 100%;
    display: flex;
    justify-content: center;
}
.category button{
    height: 40px;
    width: 80%;
    background-color:#ff511c;
    border-radius: 10px;
    font-size: large;
    border: none;
    cursor: pointer;
    color: white;
    
}
.category button:focus{
    background: #ff3f05;
}

.menuhead{
    color:black;
    /* margin-left: 235px; */
    text-align: center;


}

.ourmenu{
    /* border: 2px solid red; */
    width:75%;
    height:400px;
}

.ourmenu img{
    margin-top: 14px;
    width: 100%;
    height: 100%;
    object-fit: cover;
}



.content {
    position: absolute;
    width: 100%;
    height: 100%;
    top: -10%;
    left:0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: white;
  }
  
  .overlay {
    position: absolute;
    top: 2%;
    left:14%;
    width: 75%;
    height: 76%;
    background-color: rgba(0,0,0,.4);
    /* background-color: #000000cc; */
  }

  .content h2{
    font-family: "Cedarville Cursive", cursive;
  font-weight: 400;
  font-size: 40px;
  font-style: normal;
  width:70%; 
  /* color:black; */
  text-align: center;
  background-color: transparent;
  /* border: 2px solid red; */
  /* margin-bottom: -30px; */
  }



.searchbar{
    width:100%;
    /* border:2px solid red; */
    height:50px;
    /* text-align: center; */
    display: flex;
    align-items: center;
    justify-content: center;
}

.searchbar input{
    /* text-align: center; */
    width:50%;
    height: 90%;
    border-radius: 5px;
    font-size: 15px;
    background-color: rgba(255, 255, 255, 0.8);
     /* text-align: center; */
    /* padding-left: 50px; */
    padding-inline-start:50px;
    padding-inline-end: 10px;
    outline: none;
    border: none;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.6), 0 6px 20px 0 rgba(0, 0, 0, 0.19);

}

.searchbar input:active{
    box-shadow: 20 4px 8px 0 rgba(0, 0, 0, 0.6), inset -20 -6px -20px 0 rgba(0, 0, 0, 0.19);


}

.searchbar i{
    color: black;
    position: relative;
    left:2%;
}

.noresult{
    background-color: antiquewhite;
    width: 90%;
    height: 90px;
    padding-top: 50px;
    text-align: center;

}
.other{
